<template>
  <div class="game1">
    <!-- 第一幕 开始游戏-->
    <div class="stage0" v-show="stage === 0">
      <img src="../assets/findoutpiggy/0.png" class="bg">
      <!-- <div class="btn backBtn" @click="goBackPage">
        <img src="../assets/findoutpiggy/1.png"></div> -->
      <div class="btn guideBtn" @click="displayGuide">
        <img src="../assets/findoutpiggy/2.png"></div>
      <div class="startBtn" @click="gotoStage(1)">
        <img src="../assets/findoutpiggy/3.png"></div>
      <div class="guide" @click="hideGuide" v-show="showGuide">
        <img src="../assets/findoutpiggy/4.png"></div>
    </div>
    <!-- 第二幕 寻找猪英俊 知道了-->
    <div class="stage1" v-show="stage === 1">
      <img src="../assets/findoutpiggy/5.png" class="bg1">
      <div class="modal">
        <div class="animal">
          <img src="../assets/findoutpiggy/6.png" class="halo">
          <img :src="require('../assets/findoutpiggy/animals/' + (targetIdx + 1) + '.png')" class="icon">
          <img src="../assets/findoutpiggy/7.png" class="gesture">
        </div>
        <img :src="require('../assets/findoutpiggy/title/' + (targetIdx + 1) + '.png')" class="title">
        <div class="iknow" @click="iKnow">
          <img src="../assets/findoutpiggy/8.png">
        </div>
      </div>
    </div>
    <!-- 第三幕 游戏 -->
    <div class="stage2" id="resultBg"
      v-show="stage === 2">
      <div class="banner">
        <img src="../assets/findoutpiggy/10.png" class="icon2">
        <div class="progress">
          <p>第{{current}}关</p>
          <div>
            <img src="../assets/findoutpiggy/11.png">
            <img src="../assets/findoutpiggy/12.png" class="arrow"
              :style="{left: leftArrow + 'px'}">
          </div>
        </div>
        <p class="timer">
          <span v-show="!isOver">{{timer}}S</span>
          <span v-show="isOver">OVER</span></p>
      </div>
      <div class="animalsContainer"
        :style="{paddingTop: ptDiv + 'px', paddingBottom: pbDiv + 'px',paddingLeft: ptlrDiv + 'px', paddingRight: ptlrDiv + 'px'}">
        <ul
          v-for="item in arrTwo"
          :style="{paddingLeft: item.pl + 'px'}">
          <li v-for="(n,idx) in item.arr"
            :style="{transform: 'translateY('+ n.translateY +'px'}"
            @click="checkTarget(n)">
            <img :src="n.pic"
              :style="{width: averLen + 'px', height: averLen + 'px', transform: 'rotate('+ n.rotate +'deg)'}">
          </li>
        </ul>
      </div>
    </div>
    <!-- 第四幕 游戏结果 -->
    <div class="stage3" v-show="stage === 3">
      <div class="pic" id="pic">
        <img class="resultBg" :src="bgSrc">
        <div class="cover">
          <img class="showPic" src="../assets/findoutpiggy/18.png" v-show="current <= 3">
          <img class="showPic" src="../assets/findoutpiggy/16.png" v-show="current > 3">
          <p class="tip">{{tip}}</p>
          <div class="ad">
            <p>邀请好友参加比拼眼力大闯关</p>
            <span>保存图片到相册分享给好友</span>
            <div class="qrCode">
              <img src="../assets/findoutpiggy/10.png">
              <span>长按图片保存</span>
            </div>
          </div>
        </div>
      </div>
      <div class="picContainer">
      </div>
      <div class="btnContainer">
        <div class="saveshare" @click="saveShare">
          <img src="../assets/findoutpiggy/20.png">
        </div>
        <div class="playagain" @click="playAgain">
          <img src="../assets/findoutpiggy/17.png">
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import html2canvas from 'html2canvas'
export default {
  data () {
    return {
      // default state
      showGuide: false, // 第一幕规则显示
      stage: 0, // 第几幕
      current: 1, // 第几关
      // config
      timer: 30, // 游戏时间倒计时，多少秒后结束游戏
      intervalId: null,
      ptDiv: 80, // 父级div.animalsContainer padding-top
      pbDiv: 20, // 父级div.animalsContainer padding-bottom
      ptlrDiv: 10, // 父级div.animalsContainer padding-left&right
      xMax: 10, // 子ul的左右偏移最大值
      arrAnimals: ['鸡', '野猪', '奶牛', '鸟', '鹿', '狐狸', '浣熊', '松鼠', '家猪', '猫头鹰', '羊', '鸭'], // assets内图片顺序
      rMin: -40,
      rMax: 40, // [-rMin, rMax] 小动物在li内左右旋转角度
      // computed 每次开始游戏重置
      targetIdx: 0, // 指定这一次游戏中找哪个小动物的index
      // computed 每一关重置
      // 以第2关 this.total = 18 为例
      averLen: 0, // 小动物宽高
      arrAnimalsCount: [], // 得到每一关上各个小动物数 [1, 2, 2, 1, 2, 1, 1, 2, 1, 1, 2, 2]
      arrTwo: [], // 最终生成的循环数组 div.animalsContainer内 [{x: ,arr:[]},{x: ,arr:[]}]
      targetPos: {x: -1, y: -1}, // 选定小动物的坐标
      bgSrc: '',
      isOver: false // 当前是否结束
    }
  },
  computed: {
    // 上方箭头偏移量
    leftArrow: function () {
      return this.current < 7 ? (this.current - 1) * 10 : 60
    },
    // 每一关小动物的总数
    total: function () {
      // 前三关出现前一关数量的1.5倍，第一关出现12个：第一关 出现12个图案，第二关出现12*1.5=18，第三关出现18*1.5=27；
      // 第四关及之后的数量为前一关的2倍：第四关出现12*1.5>3 *2=54，第五关出现54*2=108，第六关出现108*2=216.
      return this.current <= 3 ? 12 * Math.pow(1.5, this.current - 1) : 27 * Math.pow(2, this.current - 3)
    },
    tip: function () {
      return this.current <= 3 ? '还需再练练，您闯到第' + this.current + '关~' : '真的是火眼金睛，您闯到第' + this.current + '关~'
    }
  },
  mounted () {
    // 每次玩游戏
    this.getRandomTargetIdx()
    // 每一关
    this.initGame()
  },
  methods: {
    getPic: function () {
      let imgHeight = document.body.clientHeight// 获取DOM高度
      let imgWidth = document.body.clientWidth// 获取DOM宽度
      let scale = window.devicePixelRatio // 获取设备像素比
      html2canvas(window.document.querySelector('#pic'), {
        useCORS: true,
        scale: scale,
        width: imgWidth,
        height: imgHeight
      }).then((canvas) => {
        let elemImg = `<img src='${canvas.toDataURL('image/png')}' id='canvas-img' style='height: ${imgHeight}px;width:${imgWidth}px'/>`
        window.document.querySelector('.picContainer').innerHTML = elemImg
      })
    },
    getBgSrc: function () {
      let imgHeight = document.body.clientHeight// 获取DOM高度
      let imgWidth = document.body.clientWidth// 获取DOM宽度
      let scale = window.devicePixelRatio // 获取设备像素比
      html2canvas(window.document.querySelector('#resultBg'), {
        useCORS: true,
        scale: scale,
        width: imgWidth,
        height: imgHeight
      }).then((canvas) => {
        this.bgSrc = canvas.toDataURL('image/png')
        this.getPic()
        this.stage = 3
      })
    },
    // 主逻辑 start
    // public methods
    // [min, max] random int number
    getRandomNum: function (min, max) {
      return Math.floor(Math.random() * (max - min + 1)) + min
    },
    // 指定这一次游戏中找哪个小动物
    getRandomTargetIdx: function () {
      let max = this.arrAnimals.length - 1
      this.targetIdx = this.getRandomNum(0, max)
    },
    // 每一关
    initGame: function () {
      this.arrTwo = []
      this.arrAnimalsCount = this.getArrAnimalsCount()
      this.getTwoDimensionArray()
    },
    // 以第2关 this.total = 18 为例 7plus 414*736
    // 重新计算每个小动物宽高
    // 返回每一关的行数，每行多少个, 子Ul上下最大偏移量
    _getRowandColumn: function () {
      // let scale = this.current < 5 ? 0.6 : 1
      let scale = 0.6
      let docWidth = document.body.clientWidth
      let docHeight = document.body.clientHeight

      let wUl = docWidth - this.ptlrDiv * 2 - this.xMax // 算出子ul的宽度
      let divHeight = docHeight - this.ptDiv - this.pbDiv

      let averLen = parseInt(Math.sqrt(wUl * divHeight * scale / this.total)) // 90
      let lines = Math.ceil(divHeight / averLen) // 8行
      let numRow = Math.ceil(wUl / averLen) // 每行5个

      // 重新计算
      averLen = parseInt(Math.sqrt(wUl * divHeight / (lines * numRow)))
      // 行数减少，重新计算
      lines = Math.ceil(this.total / numRow)

      // 确定每行内的小动物可以上下偏移最大值
      let yMax = parseInt((divHeight - lines * averLen) / (lines + 1) / 2)

      this.averLen = averLen // 小动物宽高
      return {
        numRow: numRow, // 每行多少个
        lines: lines, // 多少行
        yMax: yMax // 上下行之间的间距
      }
    },
    // 每行动物数平均相近,小动物数/行数,余数随机分配到某一行数,base+1.[5,5,4,4]
    // lines确定的行数
    _getArrRowCount: function (lines) {
      let base = parseInt(this.total / lines)
      let tail = this.total % lines
      let arrRowCount = []
      for (let i = 0; i < lines; i++) { arrRowCount[i] = base }
      if (tail > 0) {
        let arr = []
        for (let i = 0; i < tail; i++) {
          let num = this._getRowPlusIndex(arr, lines - 1)
          arr.push(num)
        }
        for (let i = 0; i < arr.length; i++) {
          let idx = arr[i]
          arrRowCount[idx] += 1
        }
      }
      return arrRowCount
    },
    _getRowPlusIndex (arr, max) {
      let num = this.getRandomNum(0, max)
      if (arr.indexOf(num) === -1) {
        return num
      } else {
        return this._getRowPlusIndex(arr, max)
      }
    },
    // 最终生成的循环数组 div.animalsContainer内 [{x: ,arr:[]},{x: ,arr:[]}]
    getTwoDimensionArray: function () {
      let arrTwo = []

      let rowandcolumn = this._getRowandColumn()
      // let numRow = rowandcolumn.numRow
      let lines = rowandcolumn.lines
      let yMax = rowandcolumn.yMax

      let arrRowCount = this._getArrRowCount(lines)

      for (let i = 0; i < arrRowCount.length; i++) {
        let pl = this.getRandomNum(0, this.xMax)
        let obj = {pl: pl, arr: []}
        let num = arrRowCount[i]

        for (let j = 0; j < num; j++) {
          let idx = this.getIdxFromAnimalsCount()

          // 图片地址
          let pic = require('../assets/findoutpiggy/animals/' + (idx + 1) + '.png')
          // 每个小动物旋转角度
          let rDistance = this.rMax - this.rMin
          let rotate = this.getRandomNum(0, rDistance) - this.rMax
          // 每行内li上下偏移量
          let translateY = this.getRandomNum(yMax * 0.6, yMax)
          let dir = j % 2 === 0 ? translateY : -translateY
          // console.log(i + '<<<<<<<<' + j + '>>>>' + dir)

          let objx = {pos: i + ':' + j, pic: pic, rotate: rotate, translateY: dir}

          if (idx !== this.targetIdx) {
            objx.isTarget = false
          } else {
            objx.isTarget = true
            this.targetPos = {x: i, y: j}
          }
          obj.arr.push(objx)
        }
        arrTwo.push(obj)
      }

      // 扩大随机概率，随机确定的位置和this.targetPos的位置对换
      let c = this.getRandomNum(0, lines - 1)
      let d = this.getRandomNum(0, arrRowCount[c] - 1)

      let a = this.targetPos.x
      let b = this.targetPos.y

      // console.log(a + ':' + b + '换到' + c + ':' + d)
      let oldTarget = arrTwo[a].arr[b] // 寻找小动物原先在的位置
      let temp = arrTwo[c].arr[d]

      let y1 = oldTarget.translateY
      let y2 = temp.translateY

      arrTwo[a].arr[b] = temp
      arrTwo[a].arr[b].translateY = y1
      arrTwo[c].arr[d] = oldTarget
      arrTwo[c].arr[d].translateY = y2

      this.arrTwo = arrTwo
    },
    getIdxFromAnimalsCount () {
      let max = this.total - 1
      let idx = this.getRandomNum(0, max)
      let len = this.arrAnimalsCount.length
      idx = idx % len
      if (this.arrAnimalsCount[idx] === 0) {
        return this.getIdxFromAnimalsCount()
      } else {
        this.arrAnimalsCount[idx] --
        return idx
      }
    },
    // 以第2关 this.total = 18 为例
    // 得到每一关上各个小动物数 [1, 2, 2, 1, 2, 1, 1, 2, 1, 1, 2, 2]
    getArrAnimalsCount: function () {
      let base = Math.floor((this.total - 1) / (this.arrAnimals.length - 1))
      let tail = (this.total - 1) % (this.arrAnimals.length - 1)

      let arrAnimalsCount = []
      for (let i = 0; i < this.arrAnimals.length; i++) {
        arrAnimalsCount[i] = i !== this.targetIdx ? base : 1
      }

      // 每一关小动物数平均相近：this.total/this.arrAnimals.length - 1 余数随机指定base+1的index [7, 1, 11, 2, 10, 4]
      if (tail > 0) {
        let arrAnimalsPlusIndex = []
        for (let j = 0; j < tail; j++) {
          let num = this._getPlusNum(arrAnimalsPlusIndex)
          arrAnimalsPlusIndex.push(num)
        }
        for (let z = 0; z < arrAnimalsPlusIndex.length; z++) {
          let idx = arrAnimalsPlusIndex[z]
          arrAnimalsCount[idx] += 1
        }
      }
      return arrAnimalsCount
    },
    _getPlusNum (arr) {
      let max = this.arrAnimals.length - 1
      let num = this.getRandomNum(0, max)
      if (num !== this.targetIdx && arr.indexOf(num) === -1) {
        return num
      } else {
        return this._getPlusNum(arr)
      }
    },
    checkTarget: function (item) {
      if (item.isTarget) {
        this.current ++
        this.initGame()
      } else {
        this.stopGame()
      }
    },
    // 主逻辑end
    // 倒计时逻辑
    startCountdown: function () {
      let that = this
      this.intervalId = window.setInterval(function () {
        if (that.timer > 0) {
          that.timer --
        } else {
          that.stopGame()
        }
      }, 1000)
    },
    stopGame: function () {
      // console.log('游戏结束')
      let that = this
      window.clearInterval(that.intervalId)
      this.isOver = true
      this.$nextTick(() => {
        this.getBgSrc()
      })
    },
    iKnow: function () {
      this.stage = 2
      this.startCountdown()
      // console.log('开始游戏')
    },
    playAgain: function () {
      // 每次玩游戏
      // console.log('回到第一幕')
      this.getRandomTargetIdx()
      this.timer = 30
      this.current = 1
      this.isOver = false
      this.initGame()
      this.stage = 0
      window.document.querySelector('.picContainer').innerHTML = ''
    },
    saveShare: function () {},
    displayGuide: function () { this.showGuide = true },
    hideGuide: function () { this.showGuide = false },
    gotoStage: function (index) { this.stage = index }
  }
}
</script>
