.game1 .stage3 .picContainer{
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}
.game1 .stage3 .btnContainer{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%);
}
.game1 .stage3 .saveshare{
  margin-bottom: 40px;
}
.game1 .stage3 .playagain img,
.game1 .stage3 .saveshare img{
  width: 266px;
  height: 88px;
}

</style>



