olute;\n  right: -0.133333rem;\n  bottom: -0.133333rem;\n}\n\n.game1 .stage1 .modal .title {\n  width: 2.4rem;\n}\n\n.game1 .stage1 .modal .iknow {\n  width: 3.546667rem;\n  margin: 1.866667rem auto 0;\n}\n\n.game1 .stage1 .modal .iknow img {\n  width: 100%;\n}\n\n/*第四幕*/\n\n.game1 .stage3 .pic {\n  width: 100%;\n  height: 100%;\n  background-image: linear-gradient(-180deg, #63C383 0%, #318A75 51%, #308975 52%, #015368 100%);\n}\n\n.game1 .stage3 .pic .resultBg {\n  width: 100%;\n  height: 100%;\n}\n\n.game1 .stage3 .pic .cover {\n  position: absolute;\n  left: 0;\n  top: 0;\n  width: 100%;\n  height: 100%;\n  background: rgba(0,0,0,0.6);\n  text-align: center;\n}\n\n.game1 .stage3 .pic .cover .showPic {\n  width: 60%;\n  margin-top: 0.533333rem;\n}\n\n.game1 .stage3 .pic .tip {\n  width: 100%;\n  height: 0.533333rem;\n  margin-top: 0.533333rem;\n  text-align: center;\n  font-family: PingFangSC-Regular;\n  font-size: 0.4rem;\n  line-height: 0.533333rem;\n  color: #FFFFFF;\n}\n\n.game1 .stage3 .pic .ad {\n  width: 8.933333rem;\n  height: 2.133333rem;\n  background-image: url('./src/assets/findoutpiggy/15.png');\n  background-size: 100% 100%;\n  background-position: 0 0;\n  position: absolute;\n  left: 50%;\n  bottom: 0.533333rem;\n  transform: translate(-50%, 0);\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n}\n\n.game1 .stage3 .pic .ad p {\n  font-family: PingFangSC-Semibold;\n  font-size: 0.373333rem;\n  line-height: 0.533333rem;\n  color: #D57B43;\n  letter-spacing: 0;\n  text-align: center;\n  margin-bottom: 0.133333rem;\n}\n\n.game1 .stage3 .pic .ad span {\n  font-family: PingFangSC-Regular;\n  font-size: 0.32rem;\n  color: #727272;\n  letter-spacing: 0;\n  text-align: center;\n}\n\n.game1 .stage3 .pic .ad .qrCode {\n  position: absolute;\n  right: 0;\n  top: 50%;\n  transform: translate(0, -50%);\n  width: 2.266667rem;\n  height: 2.133333rem;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: space-around;\n}\n\n.game1 .stage3 .pic .ad .qrCode img {\n  width: 1.333333rem;\n  height: 1.333333rem;\n}\n\n.game1 .stage3 .pic .ad .qrCode span {\n  font-family: PingFangSC-Regular;\n  font-size: 0.266667rem;\n  line-height: 0.266667rem;\n  color: #969696;\n  letter-spacing: 0;\n  text-align: center;\n}\n\n.game1 .stage3 .picContainer {\n  width: 100%;\n  height: 100%;\n  position: absolute;\n  left: 0;\n  top: 0;\n}\n\n.game1 .stage3 .btnContainer {\n  position: absolute;\n  left: 50%;\n  top: 50%;\n  transform: translateX(-50%);\n}\n\n.game1 .stage3 .saveshare {\n  margin-bottom: 0.533333rem;\n}\n\n.game1 .stage3 .playagain img,\n.game1 .stage3 .saveshare img {\n  width: 3.546667rem;\n  height: 1.173333rem;\n}"],"sourceRoot":""}]);

